.root {
    padding-top: 60px;
}

.cardBody {
    padding-bottom: 20px;
    width: 80%;
    margin: auto;
}


.innerCardBody {
    width: 75%;
    margin-bottom: 0;
    margin-top: 0;
    margin: auto;
    padding-bottom: 32px;
}

.innerCardBody p {
    margin: 0
}

.avatarLogo {
    margin: auto;
    width: 8rem !important;
    height: 8rem !important;
    margin-right: 2rem;
}
.jobContent {
    font-size: 0.9rem;
    margin: 0;
    text-align: justify;
}


.avatarMobileLogo {
    width: 0px !important;
    height: 0px !important;
}

.centerOnMobile {
    text-align: left;
    margin: 0;
}

.skill {
    font-size: 1.25rem;
    margin: 8px;
}

@media only screen and (max-width: 800px) {
    .cardBody {
        width: 90%;
    }
    .innerCardBody {
        width: 90%;
    }
    
    .avatarLogo {
        width: 0px !important;
        height: 0px !important;
        margin-right: 0;
        visibility: hidden;
    }
    .jobContent {
        text-align: justify;
        font-size: 0.75rem;
    }
    .avatarContent h4 {
        text-align: center;
    }
    .avatarContent {
        margin-left: 0 !important;
    }
    .avatarMobileLogo {
        width: 6rem !important;
        height: 6rem !important;
        margin-bottom: 1.3rem;
    }    
    .centerOnMobile {
        text-align: center;
    }
    .skill {
        font-size: 1rem;
        margin: 4px;
    }
}